.header-section {
	min-height: 30rem;
	position: relative;
	display: table;
	width: 100%;
	height: auto;
	padding-top: 8rem;
	padding-bottom: 8rem;
	background: -webkit-gradient(
		linear,
		left top,
		right top,
		from(rgba(255, 255, 255, 0.1)),
		to(rgba(255, 255, 255, 0.1)),
		url(/beta/static/media/chandler-denise-485520-unsplash.1631b904.jpg)
	);
	background: -webkit-linear-gradient(
			left,
			rgba(255, 255, 255, 0.1) 0,
			rgba(255, 255, 255, 0.1) 100%
		),
		url(/beta/static/media/chandler-denise-485520-unsplash.1631b904.jpg);
	background: linear-gradient(
			90deg,
			rgba(255, 255, 255, 0.1) 0,
			rgba(255, 255, 255, 0.1) 100%
		),
		url(/beta/static/media/chandler-denise-485520-unsplash.1631b904.jpg);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
}
.header-section h1 {
	font-size: 4rem;
	margin: 0;
	padding: 0;
}
@media (min-width: 992px) {
	.header-section h1 {
		font-size: 5.5rem;
	}
}
@media (min-width: 992px) {
	.header-section {
		height: 100vh;
	}
}

.content-section {
	padding-top: 7.5rem;
	padding-bottom: 7.5rem;
}
.content-section-heading h2 {
	font-size: 3rem;
}
.content-section-heading h3 {
	font-size: 1rem;
	text-transform: uppercase;
}

body,
html {
	width: 100%;
	height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 700;
}

